import { Controller, Commands } from '@ali/4ever-cangjie';
import React from 'react';


export default function onTab(event: React.KeyboardEvent, editor: Controller, next: () => void) {
  const { value } = editor;
  const { selection } = value;
  if (selection.isExpanded) return next();
  const currentItem = value.startBlock;
  if (currentItem.type === 'hr') {
    event.preventDefault();
    return editor.command(Commands.insertBlock, 'paragraph')
      .command(Commands.focus);
  }
  return next();
}
